.land-page {
  padding: 11px 5% 0 5%;
  margin: 0;
  background-image: url("../img/mob-background.webp");
  background-repeat: no-repeat;
  background-size: cover;
  color: white;

  @media (min-width: @desktop-width) {
    padding: 11px 10% 0 9%;
    background-image: url("../img/backgroundDesktop.webp");
    min-height: 2100px;
  }
}

.land-header__container {
  display: flex;
  flex-wrap: nowrap;
  width: auto;
  justify-content: space-between;
  align-items: center;
}

.land-header__logotype {
  width: 61px;
  display: block;

  @media (min-width: @desktop-width) {
    width: 177px;
  }
}

.land-header__button {
  font-family: "Exo", "Exo2-Medium", sans-serif;
  background-color: inherit;
  border: 1px solid rgb(255, 255, 255, 51%);
  line-height: 10.8px;
  color: rgb(255, 255, 255, 51%);
  border-radius: 20px;
  padding: 5px 10px 5px 10px;
  font-size: 9px;

  @media (min-width: @desktop-width) {
   font-size: 24px;
    padding: 18px;
  }
}

.land-header__heading {
  margin-top: 70px;
  font-family: "Exo", "Exo2-Bold", sans-serif;
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 1px;

  @media (min-width: @desktop-width) {
    font-size: 54px;
    width: 568px;
  }
}

.land-header__description {
  font-family: "Exo", "Exo2-Regular", sans-serif;
  font-size: 12px;
  font-weight: 400;

  @media (min-width: @desktop-width) {
    font-size: 32px;
    width: 480px;
  }
}

.land-main__wrapper {
  display: flex;
  flex-wrap: wrap;

}

.land-main__photo {
  display: block;
  margin-right: auto;
  margin-top: 50px;
  width: 219px;
  height: 119px;

  @media (min-width: @desktop-width) {
    width: 607px;
    height: 331px;
  }
}

.land-main__description-container {
  margin-top: 15px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.land-main__svg {
  margin-right: 10px;
}

.land-main__svg--right {
  transform: scale(0);

  @media (min-width: @desktop-width) {
    transform: scale(1);
  }
}

.land-page__container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-end;

  @media (min-width: @desktop-width) {
    height: auto;
    align-items: stretch;
  }
}

.lane-page__img-container {
  width: 50%;
  padding-bottom: 0;
  margin-bottom: 0;
}

.land-page__img {
  margin: 0;
  padding: 0;
  width: 100%;
}

.land-page__photo {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.land-page__form-heading {
  font-family: "Exo", "Exo2-Regular", sans-serif;
  font-weight: 400;
  font-size: 10px;

  @media (min-width: @desktop-width) {
    font-size: 32px;
    margin: 0;
  }
}

.land-main__column-container {
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.land-main__mini-container {
  display: flex;
  flex-wrap: nowrap;
  align-items: start;
}

.land-main__profile-description {
  margin: 0 0 0 3px;
  font-family: "Exo", "Exo2-Regular", sans-serif;
  font-weight: 400;
  font-size: 9px;

  @media (min-width: @desktop-width) {
    font-size: 24px;
  }
}

.land-page__form > * {
  margin: 15px 0;
}

.land-page__checkbox-container {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: start;
}

.land-page__input {
  font-family: "Exo", "Exo2-Light", sans-serif;
  font-weight: 200;
  background-color: inherit;
  border: 1px solid rgb(196, 196, 196, 0%);
  color: rgb(255, 255, 255, 51%);
  font-size: 9px;
  padding: 5px;
  border-radius: 20px;
  margin-top: 5px;

  @media (min-width: @desktop-width) {
    margin-top: 10px;
    font-size: 24px;
    padding: 14px;
    border-radius: 20px;
    width: 552px;
    color: white;
  }
}

.land-page__checkbox {
  width: 0;
  background-color: inherit;
  border: 0.5px solid #989898;
  z-index: -1;
  //opacity: 0;
  transform: scale(0);
}

.land-page__checkbox+.land-page__label::before {
  margin-right: 5px;
  margin-bottom: 5px;
  content: '';
  display: inline-block;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 0.25em;
  background-repeat: no-repeat;
  background-position: center center;
}

.land-page__checkbox:checked+.land-page__label::before {
  border-color: #989898;
  background-color: #F5EFEF;
  background-image: url("../img/checked.svg");
}

.land-page__label {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  font-family: "Exo", "Exo2-Light", sans-serif;
  font-weight: 200;
  font-size: 7px;
  color: rgb(255, 255, 255, 51%);

  @media (min-width: @desktop-width) {
    font-size: 20px;
    color: rgb(255, 255, 255, 51%);
  }
}

.land-page__width-container {
  width: 100%;
}

.land-page__submit {
  font-family: "Exo", "Exo2-Light", sans-serif;
  font-weight: 200;
  border: 1px solid rgb(196, 196, 196, 0%);
  font-size: 9px;
  padding: 5px 10px 5px 10px;
  color: #FFFFFF;
  border-radius: 200px;
  background-color: inherit;
  box-shadow: 0 1px 4px 0px (rgb(0, 0, 0, 25%)),
              0 -1px 4px 0px (#FFFFFF),
              -1px 0 4px 0px (#FFFFFF),
              -1px 1px 4px 0px (#FFFFFF),
              1px 1px 4px 1px (#FFFFFF);

  @media (min-width: @desktop-width) {
    font-size: 24px;
    border-radius: 200px;
    padding: 15px;
    background-color: inherit;
    transition-duration: 1s;
  }
}

.land-page__submit:hover {
  cursor: pointer;
  box-shadow: 0 3px 4px 0 (rgb(0, 0, 0, 25%)),
              0 -3px 4px 0 (#FFFFFF),
              -3px 0 4px 0 (#FFFFFF),
              -3px 3px 4px 0 (#FFFFFF),
              3px 3px 4px 1px (#FFFFFF);
  transform: scale(1.04);
}

.land-page__description-container {
  margin-top: 20px;
}

.land-page__mini-container {
  display: flex;
  flex-wrap: nowrap;
  align-items: baseline;
  justify-content: space-between;
}

.land-page__mini-heading {
  width: 120px;
  font-size: 11px;
  font-family: "Exo", "Exo2-Bold", sans-serif;
  font-weight: 600;
  letter-spacing: 1px;

  @media (min-width: @desktop-width) {
    width: 50%;
    font-size: 40px;
    letter-spacing: 3px;
  }
}

.land-page__mini-text {
  width: 50%;
  font-family: "Exo", "Exo2-Regular", sans-serif;
  font-weight: 400;
  font-size: 9px;
  margin-top: 0;

  @media (min-width: @desktop-width) {
    font-size: 24px;
    padding: 0 0 0 4%;
    width: 50%;
  }
}
