.main-header {
  color: #ffff;
  display: flex;
  padding: 0 10% 0 10%;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  background: #181818;
  background: linear-gradient(90deg, rgba(24, 24, 24, 0.88308827) 15%, rgba(1, 1, 0, 0.90549723) 75%);
  height: 10%;
}
.main-header__profile {
  width: auto;
