.page-footer {
  width: 100%;
  position: relative;
  bottom: 0;
  background: rgb(24,24,24);
  background: linear-gradient(90deg, rgba(24,24,24,0.8830882694874825) 15%, rgba(1,1,0,0.9054972330729166) 75%);
  height: 80px;
  padding: 10px 0 10px 0;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: @desktop-width) {
    height: 40px;
    padding: 10px 0 30px 0;
  }
}

.page-footer__text {
  text-decoration: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 13px;

  @media (min-width: @desktop-width) {
    font-size: 14px;
  }
}
